@use "../global/mixins.scss" as *;
@use "../global/variables.scss" as *;

.feedback-form {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 49px;
  padding: 24px 20px 0 20px;

  @media (min-width: $tablet-width) {
    padding: 24px 60px 49px 60px;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 58px;
    row-gap: 47px;
  }

  @media (min-width: $desktop-width) {
    @include edge-distance(120px);
    column-gap: 100px;
    row-gap: 56px;
    padding-bottom: 41px;
  }
}

.feedback-form__list {
  @include reset-list;
}

.feedback-form__group {
  position: relative;
  border: none;
  margin: 0;
  padding: 0;

  @media (max-width: $mobile-width-only) {
    .feedback-form__title::before {
      position: absolute;
      content: "";
      top: -25px;
      left: -20px;
      width: calc(100% + 40px);
      height: 1px;
      background-color: #e5e5e5;
    }
  }

  @media (min-width: $tablet-width) {
    &:first-child::before {
      position: absolute;
      content: "";
      top: -60px;
      right: -29px;
      width: 1px;
      height: 118%;
      background-color: #dbdbdb;
    }
  }

  @media (min-width: $desktop-width) {
    &:first-child::before {
      right: -49px;
    }
  }
}

.feedback-form__group--special {
  margin-bottom: -26px;
  padding-top: 10px;
  grid-column: 1/-1;

  @media (min-width: $tablet-width) {
    border-top: solid 1px #dbdbdb;

    .feedback-form__title {
      width: 402px;
      background-color: #ffffff;
    }
  }

  @media (min-width: $desktop-width) {
    margin-bottom: -8px;

    .feedback-form__title {
      width: 400px;
      margin-bottom: 45px;
    }
  }
}

.feedback-form__group--contacts {
  margin-bottom: -7px;
}

.feedback-form__group--impression {
  .feedback-form__list {
    @media (min-width: $tablet-width) {
      @include edge-distance(36px);
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 16px;
    }

    @media (min-width: $desktop-width) {
      padding-left: 53px;
      grid-template-columns: repeat(3, 1fr);
      column-gap: 53px;
    }
  }

  .feedback-form__item {
    margin-bottom: 36px;
  }
}

.feedback-form__group--landmark {
  .feedback-form__list {
    @media (min-width: $tablet-width) {
      @include edge-distance(84px);
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 31px;
    }

    @media (min-width: $desktop-width) {
      @include edge-distance(44px);
      grid-template-columns: repeat(4, 1fr);
      column-gap: 64px;
    }
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 8px;
  }

  .feedback-form__title {
    @media (min-width: $desktop-width) {
      margin-bottom: 59px;
    }
  }

  .feedback-form__item {
    margin-bottom: 36px;
  }
}

.feedback-form__group--emotions {
  margin-bottom: 1px;

  .feedback-form__title {
    margin-bottom: 10px;

    @media (min-width: $tablet-width) {
      margin-bottom: 17px;
    }
  }

  @media (min-width: $tablet-width) {
    text-align: center;
    margin-top: -3px;
    margin-bottom: -5px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: -8px;
  }
}

.feedback-form__item {
  margin-bottom: 16px;
  position: relative;
  display: flex;
  flex-direction: column;
}

.feedback-form__input {
  @include input-shape;
  padding: 16px;
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  background-color: #f2f2f2;
  border: none;
}

.feedback-form__item--telephone {
  &::before {
    position: absolute;
    content: "";
    width: 58px;
    height: 58px;
    bottom: 0;
    right: 0;
    background-color: #e6e6e6;
    background-image: url("../img/stack.svg#phone");
    background-size: 18px auto;
    background-repeat: no-repeat;
    background-position: center;
  }
}

.feedback-form__item--email {
  &::before {
    position: absolute;
    content: "";
    width: 58px;
    height: 58px;
    bottom: 0;
    right: 0;
    background-color: #e6e6e6;
    background-image: url("../img/stack.svg#mail");
    background-size: 16px auto;
    background-repeat: no-repeat;
    background-position: center;
  }
}

.feedback-form__label {
  &:not(:last-child) {
    padding-left: 16px;
  }
}

.feedback-form__textarea {
  padding: 16px;
  width: 100%;
  max-width: 768px;
  min-height: 150px;
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  resize: none;
  background-color: #f2f2f2;
  border: none;

  @media (min-width: $tablet-width) {
    height: 160px;
  }
}

.feedback-form__title {
  margin: 0 auto;
  margin-bottom: 20px;
  display: block;
  position: relative;
  width: 100%;

  @media (min-width: $tablet-width) {
    margin-bottom: 28px;
  }
}
